import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './components/home/home.component';
import { ListPromesasComponent } from './components/list-promesas/list-promesas.component';
import { FormPromesaComponent } from './components/form-promesa/form-promesa.component';

const routes: Routes = [
  {
    path: '',
    component: HomeComponent
  },
  {
    path: 'promesas',
    component: ListPromesasComponent
  },
  {
    path: 'promesas/crear',
    component: FormPromesaComponent
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
