import { Component, OnInit,HostBinding } from '@angular/core';
import { Promesa } from 'src/app/models/Promesa';

@Component({
  selector: 'app-form-promesa',
  templateUrl: './form-promesa.component.html',
  styleUrls: ['./form-promesa.component.css']
})
export class FormPromesaComponent implements OnInit {

  @HostBinding('class') classes = 'row';

  promesa:Promesa = {
    titulo : "",
    descripcion: "",
    candidato: 0
  }

  file:File = null;
  urlImage:string = null;


  constructor() { }

  ngOnInit() {
  }



  fileChange(event){
    this.file = event.target.files[0];
    
    if(event.target.files && event.target.files[0]){
      let reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = e => this.urlImage = reader.result as string;
    }
  }

}
